import React, { useCallback } from 'react';

import './CallParticipant.css';

import FourXThree from '../Images/4x3.png';
import fullViewOverlayIcon from '../Icons/full-view-overlay.svg';
import noViewOverlayIcon from '../Icons/no-view-overlay.svg';
import upIcon from '../Icons/no-view-up.svg';
import downIcon from '../Icons/no-view-down.svg';
import fullIcon from '../Icons/full-view.svg';
import tabIcon from '../Icons/no-view-tab.svg';
import micOnIcon from '../Icons/mic-on.svg';
import micOffIcon from '../Icons/mic-off.svg';
import camOnIcon from '../Icons/cam-on.svg';
import camOffIcon from '../Icons/cam-off.svg';

export function CallParticipant(props: {name: string, imgSrc: string, isLocal: boolean, isSpeaking: boolean, localPosition: number, fakePosition: number, sharing: 'none' | 'same' | 'diff', pages: number, requestScroll: (pos: number, name?: string) => void}) {

    const containerRef = useCallback((container: HTMLDivElement) => {
        if (container) {
            //TODO
            const img = document.createElement('img');
            img.className = "call-participant-video";
            img.src = props.imgSrc;
            container.appendChild(img);
        }
    }, [props.imgSrc]);

    let iconSrc: string | undefined = undefined;
    let jumpTarget: number | undefined = undefined;
    let tooltip: string | undefined = undefined;
    if (!props.isLocal) {
        if (props.sharing !== 'none') {
            if (props.sharing === 'same') {
                iconSrc = fullViewOverlayIcon;
            } else {
                iconSrc = noViewOverlayIcon;
            }
        } else {
            const ownTop = props.localPosition;
            const ownBottom = props.localPosition + .2;
            const userTop = props.fakePosition;
            const userBottom = props.fakePosition + .2;
            if (ownTop > userTop + (userBottom - userTop)/3) {
                iconSrc = upIcon;
                jumpTarget = userTop + (userBottom - userTop)/2 - (ownBottom - ownTop)/2;
                tooltip = 'p.' + (Math.floor(props.pages * props.fakePosition) + 1);
            } else if (ownBottom < userTop + (userBottom - userTop)/3*2 && ownTop < userTop) {
                iconSrc = downIcon;
                jumpTarget = userTop + (userBottom - userTop)/2 - (ownBottom - ownTop)/2;
                tooltip = 'p.' + (Math.floor(props.pages * props.fakePosition) + 1);
            } else {
                iconSrc = fullIcon;
            }
        }
    }

    const onIconClick = () => {
        if (jumpTarget !== undefined) {
            props.requestScroll(jumpTarget, props.name);
        }
    }

    return (
        <div className={"call-participant" + (props.isSpeaking ? ' call-participant-speaking' : '')} >
            <div className="call-participant-video-container" ref={containerRef}>
                <img className="four-x-three" src={FourXThree} alt=""></img>
                <div className="call-participant-name">
                    <h5>{`${props.name} ${props.isLocal ? ' (You)' : ''}`}</h5>
                    <span>ID: {props.name}</span>
                </div>
                <div className={'call-participant-icon' + (jumpTarget === undefined ? '' : ' cursor-pointer')} onClick={onIconClick} >
                    {iconSrc ? <img src={iconSrc} alt=""></img> : null}
                    {jumpTarget ? <div className="call-participant-icon-tooltip">{tooltip}<span> (click to jump)</span></div> : null}
                </div>
                {/*<ReactionOverlay user={props.user}/>*/}
                {props.isLocal ? 
                    <div className="pause-buttons">
                        <button className={false ? 'button-red' : ''} ><img src={false ? micOffIcon : micOnIcon} alt={false ? 'Unmute' : 'Mute'}></img>{}</button>
                        <button className={false ? 'button-red' : ''} ><img src={false ? camOffIcon : camOnIcon} alt={false ? 'Turn Video On' : 'Turn Video Off'}></img>{}</button>
                        <span>Microphone {false ? 'muted' : 'on'}, Camera {false ? 'off' : 'on'}.</span>
                    </div>
                : null}
            </div>
        </div>);
}