import React from 'react';
import { PushOrPeekState } from './DocumentOverlay';
import { TelecursorOverlay } from './TelecursorOverlay';
import { ReactionToolbar } from './ReactionToolbar';

import './PushedOrPeekedView.css';

import loadingGif from '../Images/loading.gif';
import fakeView from '../Images/joel-screen.png';

export function PushedOrPeekedView(props: {pushOrPeekState: PushOrPeekState, ownColor: string}) {
    const videoSize = {height: 9, width: 16}

    return props.pushOrPeekState.user ? (
        <div className="pushpeek">
            <img src={loadingGif} alt=""></img>
            <div className="pushpeek-toolbar">
                <h3>
                    {(props.pushOrPeekState.viewType === 'peek' ? 'Previewing ' : '')}
                    <span style={{color: props.pushOrPeekState.user.color}}>{props.pushOrPeekState.user.name}</span>
                    {(props.pushOrPeekState.viewType === 'peek' ? '\'s view' : ' is sharing their view')}
                </h3>
                <div className="pushpeek-toolbar-right">
                    <ReactionToolbar/>
                    <button >
                        Jump Here
                    </button>
                    <button className={props.pushOrPeekState.viewType === 'push' || props.pushOrPeekState.pinned ? 'button-red' : ''} >
                        {props.pushOrPeekState.viewType === 'push' || props.pushOrPeekState.pinned ? 'Dismiss' : 'Pin'}
                    </button>
                </div>
            </div>
            <div className="pushpeek-video-container" style={{paddingBottom: (videoSize.height/(videoSize.width*.8)*70) + '%'}}>
                <TelecursorOverlay showEvents={true} color={props.ownColor}/>
                <img className={"pushpeek-video"} src={fakeView} alt=""></img>
            </div>
        </div>
    ) : null;
}