import React from 'react';
import { PushOrPeekState } from './DocumentOverlay';
import './ASPositionIndicator.css';

import fullViewOverlayIcon from '../Icons/full-view-overlay.svg';
import noViewOverlayIcon from '../Icons/no-view-overlay.svg';
import upIcon from '../Icons/no-view-up.svg';
import downIcon from '../Icons/no-view-down.svg';
import fullIcon from '../Icons/full-view.svg';
import tabIcon from '../Icons/no-view-tab.svg';

export function ASPositionIndicator(props: {onPeekRequested: () => void, pushOrPeekState: PushOrPeekState, isPushing: boolean, activeSpeaker: {name: string, position: number, otherTab: boolean, sharing: 'none' | 'same' | 'diff', sharingYours: boolean, page: number, color: string} | undefined, localPosition: number}) {
    if (props.activeSpeaker === undefined) {
        return null;
    }

    let compareResult: {boldText: string, text: string, direction: 'up' | 'down' | 'tab' | 'none', icon: string, page: string, interactTip: string, interactionAvailable: 'none' | 'hover' | 'click', color: 'green' | 'red'};
    const ownTop = props.localPosition;
    const ownBottom = props.localPosition + .2;
    const userTop = props.activeSpeaker.position;
    const userBottom = props.activeSpeaker.position + .2;
    if (props.activeSpeaker.otherTab) {
        compareResult = {
            boldText: 'in another tab',
            text: `.`,
            direction: 'tab',
            icon: tabIcon,
            page: '',
            interactTip: '(Preview not available for other tabs)',
            interactionAvailable: 'none',
            color: 'red'
        }
    } else if (props.activeSpeaker.sharing !== 'none') {
        if (props.activeSpeaker.sharingYours === true) {
            compareResult = {
                boldText: 'previewing your view',
                text: `.`,
                direction: 'none',
                icon: fullViewOverlayIcon,
                page: '',
                interactTip: '',
                interactionAvailable: 'none',
                color: 'green'
            }
        } else if (props.activeSpeaker.sharing === 'same') {
            compareResult = {
                boldText: 'watching the same person ',
                text: `as you.`,
                direction: 'none',
                icon: fullViewOverlayIcon,
                page: '',
                interactTip: '',
                interactionAvailable: 'none',
                color: 'green'
            }
        } else {
            compareResult = {
                boldText: `previewing someone else's view.`,
                text: '.',
                direction: 'none',
                icon: noViewOverlayIcon,
                page: '',
                interactTip: '',
                interactionAvailable: 'none',
                color: 'red'
            }
        }
    } else if (props.pushOrPeekState.user?.name && props.pushOrPeekState.user?.name === props.activeSpeaker.name) {
        compareResult = {
            boldText: 'shown in the preview ',
            text: `below.`,
            direction: 'none',
            icon: fullViewOverlayIcon,
            page: '',
            interactTip: props.pushOrPeekState.viewType === 'push' || props.pushOrPeekState.pinned ? 'Previewing (click dismiss below to stop).' : 'Previewing (move mouse off to stop).',
            interactionAvailable: 'none',
            color: 'green'
        }
    } else if (props.pushOrPeekState.user?.name && props.pushOrPeekState.user?.name !== props.activeSpeaker.name) {
        compareResult = {
            boldText: 'not shown in the preview ',
            text: 'below.',
            direction: 'none',
            icon: noViewOverlayIcon,
            page: '',
            interactTip: 'Click to switch preview',
            interactionAvailable: 'click',
            color: 'red'
        }
    } else if (ownTop > userTop + (userBottom - userTop)/3) {
        console.log(props.pushOrPeekState.viewType);
        compareResult = {
            boldText: `above you (page ${props.activeSpeaker.page.toString()}) `,
            text: 'in the document.',
            direction: 'up',
            icon: upIcon,
            page: props.activeSpeaker.page.toString(),
            interactTip: 'Hover to preview',
            interactionAvailable: 'hover',
            color: 'red'
        }
    } else if (ownBottom < userTop + (userBottom - userTop)/3*2 && ownTop < userTop) {
        console.log(props.pushOrPeekState.viewType);
        compareResult = {
            boldText: `below you (page ${props.activeSpeaker.page.toString()}) `,
            text: `in the document.`,
            direction: 'down',
            icon: downIcon,
            page: props.activeSpeaker.page.toString(),
            interactTip: 'Hover to preview',
            interactionAvailable: 'hover',
            color: 'red'
        }
    } else {
        compareResult = {
            boldText: 'in the same part of the document as you',
            text: '.',
            direction: 'none',
            icon: fullIcon,
            page: '',
            interactTip: '',
            interactionAvailable: 'none',
            color: 'green'
        }
    }
    if (props.isPushing && compareResult.interactionAvailable !== 'none') {
        compareResult.interactTip = 'Stop sharing your view to preview.';
        compareResult.interactionAvailable = 'none';
    }

    const mouseEnter = () => {
        if (!props.pushOrPeekState.user && compareResult.interactionAvailable === 'hover') {
            props.onPeekRequested();
        }
    }

    return (
        <div className={"as-position-indicator" + (compareResult.color === 'green' ? ' as-position-indicator-green' : ' as-position-indicator-red')} onMouseEnter={mouseEnter} >
            <div className="as-position-indicator-icon">
                <img src={compareResult.icon} alt=""></img>
            </div>
            <div>
                <div className="as-position-indicator-text"><span style={{color: props.activeSpeaker.color}}><b>{props.activeSpeaker.name}</b></span> (speaker) is <b>{compareResult.boldText}</b>{compareResult.text}</div>
                <div className="as-position-indicator-tip">{compareResult.interactTip}</div>
            </div>
        </div>);
}