import React from 'react';
import { CallGrid } from './CallGrid';
import './Extension.css';

export default function Extension(props: {localPosition: number, pages: number, activeSpeaker: string | undefined, requestScroll: (pos: number) => void, isPushing: boolean, isPushingChanged: (pushing: boolean) => void}) {
    return (
        <div className="extension">
            <CallGrid localPosition={props.localPosition} pages={props.pages} requestScroll={props.requestScroll} isPushing={props.isPushing} isPushingChanged={props.isPushingChanged} activeSpeaker={props.activeSpeaker} />
        </div>
    )
}