import React from 'react';
import { CallParticipant } from './CallParticipant';
import { PushViewButton } from './PushViewButton';

import './CallGrid.css'

import user1img from '../Images/user-1.png'
import user2img from '../Images/user-2.png'
import user3img from '../Images/user-3.png'
import user4img from '../Images/user-4.png'
import user5img from '../Images/user-5.png'

export function CallGrid(props: {localPosition: number, pages: number, requestScroll: (pos: number, name?: string) => void, isPushing: boolean, isPushingChanged: (pushing: boolean) => void, activeSpeaker: string | undefined}) {
    return <div className="call-grid">
        <PushViewButton isPushing={props.isPushing} isPushingChanged={props.isPushingChanged} />
        <CallParticipant localPosition={props.localPosition} fakePosition={0.0} isLocal={true}  isSpeaking={props.activeSpeaker === 'Demo User'   } name={'Demo User'   }  imgSrc={user1img} pages={props.pages} sharing={props.isPushing ? 'same' : 'none'} requestScroll={props.requestScroll} />
        <CallParticipant localPosition={props.localPosition} fakePosition={0.5} isLocal={false} isSpeaking={props.activeSpeaker === 'Ritik Larsen'} name={'Ritik Larsen'}  imgSrc={user2img} pages={props.pages} sharing={props.isPushing ? 'same' : 'none'} requestScroll={props.requestScroll} />
        <CallParticipant localPosition={props.localPosition} fakePosition={0.7} isLocal={false} isSpeaking={props.activeSpeaker === 'Joel Alfaro' } name={'Joel Alfaro' }  imgSrc={user3img} pages={props.pages} sharing={props.isPushing ? 'same' : 'none'} requestScroll={props.requestScroll} />
        <CallParticipant localPosition={props.localPosition} fakePosition={0.0} isLocal={false} isSpeaking={props.activeSpeaker === 'Damon Snyder'} name={'Damon Snyder'}  imgSrc={user4img} pages={props.pages} sharing={props.isPushing ? 'same' : 'none'} requestScroll={props.requestScroll} />
        <CallParticipant localPosition={props.localPosition} fakePosition={0.3} isLocal={false} isSpeaking={props.activeSpeaker === 'Nyah Keeling'} name={'Nyah Keeling'}  imgSrc={user5img} pages={props.pages} sharing={props.isPushing ? 'same' : 'none'} requestScroll={props.requestScroll} />
    </div>
}