import React from 'react';
import { PushingOverlay } from './PushingOverlay';
import { ASPositionIndicator } from './ASPositionIndicator';
import { PushedOrPeekedView } from './PushedOrPeekedView';


import './DocumentOverlay.css'

export type PushOrPeekState = {
    viewType: 'none',
    user: undefined,
    pinned: false
} | {
    viewType: 'push',
    user: {name: string, position: number, color: string},
    pinned: false
} | {
    viewType: 'peek',
    user: {name: string, position: number, color: string},
    pinned: boolean
}

export function DocumentOverlay(props: {pushOrPeekState: PushOrPeekState, onPeekRequested: () => void, onPeekDismissed: () => void, pushingViewers: {name: string, color: string}[] | undefined, ownColor: string, localPosition: number, activeSpeaker: {name: string, position: number, otherTab: boolean, sharing: 'none' | 'same' | 'diff', sharingYours: boolean, page: number, color: string} | undefined}) {
    const mouseLeave = () => {
        if (props.pushOrPeekState.viewType === 'peek' && !props.pushOrPeekState.pinned) {
            props.onPeekDismissed();
        }
    }
    
    return (
        <div className={'document-overlay-container' + (props.pushOrPeekState.viewType !== 'none' ? ' bg-grey' : '')}>
            <PushingOverlay isPushing={props.pushingViewers !== undefined} viewers={props.pushingViewers} />
            <div className="document-overlay" onMouseLeave={mouseLeave}>
                <ASPositionIndicator onPeekRequested={props.onPeekRequested} pushOrPeekState={props.pushOrPeekState} isPushing={props.pushingViewers !== undefined} activeSpeaker={props.activeSpeaker} localPosition={props.localPosition}/>
                <PushedOrPeekedView pushOrPeekState={props.pushOrPeekState} ownColor={props.ownColor}/>
            </div>
        </div>
    );
}