import React, {useCallback} from 'react';
import './GoogleDoc.css';

import docsHeader from '../Images/docs-header.png'
import page1 from '../Images/page-01.png';
import page2 from '../Images/page-02.png';
import page3 from '../Images/page-03.png';
import page4 from '../Images/page-04.png';
import page5 from '../Images/page-05.png';

export default function GoogleDoc(props: {scrollContainerChanged: (container: HTMLDivElement | null) => void}) {
    const containerRef = useCallback((container: HTMLDivElement | null) => {
        props.scrollContainerChanged(container);
    }, [props.scrollContainerChanged]);

    return (
        <div className="google-doc">
            <div className="google-doc-header">
                <img src={docsHeader} alt=""></img>
            </div>
            <div className="google-doc-pages" ref={containerRef}>
                <img src={page1} alt=""></img>
                <img src={page2} alt=""></img>
                <img src={page3} alt=""></img>
                <img src={page4} alt=""></img>
                <img src={page5} alt=""></img>
            </div>
        </div>
    )
}