import React from 'react';

export function PushViewButton(props: {isPushing: boolean, isPushingChanged: (pushing: boolean) => void}) {
    const onClick = () => {
        props.isPushingChanged(!props.isPushing);
    }

    return (
        <div className="">
            <button className={props.isPushing ? 'button-red' : ''} onClick={onClick}>{props.isPushing ? 'Sharing Your View... (click to stop)' : 'Share Your View'}</button>
        </div>
    );
}