import React, { useEffect, useState } from 'react';
import { TelecursorOverlay } from './TelecursorOverlay';

import './PushingOverlay.css';

export function PushingOverlay(props: {isPushing: boolean, viewers: {name: string, color: string}[] | undefined}) {
    return (
        <div className="pushing-overlay">
            <div className={'pushing-overlay-top-crop-height pushing-overlay-top' + (props.isPushing ? ' bg-grey' : '')}>
                {props.isPushing ? (<div className="pushing-overlay-toolbar">
                    <h3>You're sharing this view</h3>
                    <span className="viewer-span">Viewers: {props.viewers?.map((u, i) => (<span style={{color: u.color}}>{u.name + (i < (props.viewers?.length || 0) - 1 ? ', ' : '')}</span>))}</span>
                </div>) : null }
            </div>
            <div className={'pushing-overlay-full-height' + (props.isPushing ? ' pushing-overlay-visible-window' : '')}>
                <TelecursorOverlay showEvents={false} color={''} />
            </div>
            <div className={'pushing-overlay-bottom-crop-height' + (props.isPushing ? ' bg-grey' : '')}></div>
        </div>
    );
}