import React, { useEffect, useRef } from 'react';

import './TelecursorOverlay.css';

export function TelecursorOverlay(props: { showEvents: boolean, color: string }) {
    /* display others' mouse events */
    const divRef = useRef<HTMLDivElement>(null);

    /* collect own mouse events */
    const messageQueue = useRef<{ x: number, y: number }[]>([]);

    useEffect(() => {
        const notifyMouseMoved = (positions: {x: number, y: number}[]) => {
            const container = divRef.current;
            if (container) {
                const color = props.color;
                const count = positions.length;
                positions.forEach((p, i) => {
                    const div = document.createElement('div');
                    div.style.backgroundColor = color;
                    div.style.animationDelay = i / count * .3 + 's';
                    div.style.left = p.x * container.clientWidth + 'px';
                    div.style.top = p.y * container.clientHeight + 'px';
                    div.onanimationend = () => div.remove();
                    div.className = 'mtrail-element mtrail-fading';
                    container.append(div);
                })
            }
        }

        if (props.showEvents) {
            const intervalID = window.setInterval(() => {
                if (messageQueue.current.length > 0) {
                    notifyMouseMoved(messageQueue.current)
                    messageQueue.current = [];
                }
            }, 300)
            return () => window.clearInterval(intervalID);
        }
    }, [props.showEvents, props.color]);

    const onMouseMove = (ev: React.MouseEvent<HTMLDivElement>) => {
        messageQueue.current.push({ x: ev.nativeEvent.offsetX / ev.currentTarget.clientWidth, y: ev.nativeEvent.offsetY / ev.currentTarget.clientHeight });
    };

    /* display others' reaction events */
    return (<div className="telecursor-overlay" onMouseMove={props.showEvents ? onMouseMove : undefined} ref={divRef}></div>);
}