import React, { useCallback, useEffect, useState } from 'react';
import './App.css';
import { DocumentOverlay, PushOrPeekState } from './Components/DocumentOverlay';
import Extension from './Components/Extension';
import GoogleDoc from './Components/GoogleDoc';

function App() {
    const [step, stepChanged] = useState(0);
    const [hasPreviewed, hasPreviewedChanged] = useState(false);
    const [hasPushed, hasPushedChanged] = useState(false);
    const [hasJumped, hasJumpedChanged] = useState(false);

    const [localPosition, localPositionChanged] = useState(0);
    const [scrollContainer, scrollContainerChanged] = useState<HTMLDivElement | null>(null);
    const [isPushing, isPushingChanged] = useState(false);
    if (isPushing && !hasPushed) {hasPushedChanged(true);}
    const [pushOrPeekState, pushOrPeekStateChanged] = useState<PushOrPeekState>({viewType: 'none', user: undefined, pinned: false});
    const [activeSpeaker, activeSpeakerChanged] = useState<{name: string; position: number; otherTab: boolean; sharing: "none" | "same" | "diff"; sharingYours: boolean; page: number; color: string; } | undefined>(undefined);
    const PAGES = 5;

    if (scrollContainer) {
        scrollContainer.onscroll = () => localPositionChanged(scrollContainer.scrollTop/scrollContainer.scrollHeight);
    }

    const requestScroll = useCallback((pos: number, user?: string) => {
        if (scrollContainer) {
            scrollContainer.scrollTo({top: scrollContainer.scrollHeight * pos, behavior: 'smooth'});
            if (user) {
                hasJumpedChanged(true);
            }
        }
    }, [scrollContainer]);

    const peekRequested = () => {
        hasPreviewedChanged(true);
        pushOrPeekStateChanged({user: {name: 'Joel Alfaro', position: .7, color: 'chocolate'}, viewType: 'peek', pinned: false});
    };

    const peekDismissed = () => {
        pushOrPeekStateChanged({viewType: 'none', user: undefined, pinned: false});
    };

    useEffect(() => {
        switch(step) {
            case 2: {
                requestScroll(0);
                activeSpeakerChanged({
                    name: 'Joel Alfaro',
                    color: 'chocolate',
                    otherTab: false,
                    page: 4,
                    position: 0.7,
                    sharing: 'none',
                    sharingYours: false
                });
                break;
            }
            case 4: {
                activeSpeakerChanged(undefined);
                break;
            }
            case 5: {
                isPushingChanged(false);
                requestScroll(0);
                break;
            }
        }
    }, [step, requestScroll])

    let instructionText = '';
    let canContinue = false;
    switch(step) {
        case 0: {
            instructionText = 'Welcome! This tutorial will walk you through some features you will use in the study.';
            canContinue = true;
            break;
        }
        case 1: {
            instructionText = 'The first feature to keep an eye on is the icons on other people\'s videos. They show when people are in the same spot as you (green checkmark), or above/below you (red \'x\'). Try scrolling down to see the icons on the right update in real time.';
            canContinue = localPosition > .5;
            break;
        }
        case 2: {
            instructionText = 'When one of your collaborators is speaking, you will get a popup like the one at the bottom of the screen. The popup lets you know where the current speaker (Joel) is editing.'
            canContinue = true;
            break;
        }
        case 3: {
            instructionText = 'Try hovering your cursor over the notification below to quickly see what the speaker is looking at. When previewing Joel\'s view, you can communicate with cursor trails and reactions.';
            canContinue = hasPreviewed;
            break;
        }
        case 4: {
            instructionText = 'If you want to show everyone else what you are looking at, you can also share your own view. Try pressing the "Share Your View" in the top right corner.';
            canContinue = hasPushed;
            break;
        }
        case 5: {
            instructionText = 'Finally, if you just want to jump to the same spot as someone else, you can click the icon on their video feed. Try clicking the icon on Joel\'s video feed to jump page 4 where he is working.';
            canContinue = hasJumped;
            break;
        }
        case 6: {
            instructionText = 'You\'ve finished the tutorial. You can close this tab and switch back to the Zoom call.';
            canContinue = false;
            break;
        }
    }

    const continueClicked = () => {
        if (canContinue) {
            stepChanged(step + 1);
        }
    }

    return (
        <div className="App">
            <div className="instructions"><span>{instructionText}</span><button disabled={!canContinue} onClick={continueClicked}>Continue</button></div>
            <DocumentOverlay activeSpeaker={activeSpeaker} localPosition={localPosition} ownColor={'olive'} pushOrPeekState={pushOrPeekState} onPeekRequested={peekRequested} onPeekDismissed={peekDismissed} pushingViewers={isPushing ? [{name: 'Ritik Larsen', color: 'darkmagenta'}, {name: 'Joel Alfaro', color: 'chocolate'}, {name: 'Damon Snyder', color: 'mediumblue'}, {name: 'Nyah Keeling', color: 'palevioletred'}] : undefined} />
            <GoogleDoc scrollContainerChanged={scrollContainerChanged} />
            <Extension localPosition={localPosition} pages={PAGES} requestScroll={requestScroll} isPushing={isPushing} isPushingChanged={isPushingChanged} activeSpeaker={activeSpeaker?.name} />
        </div>
    );
}

export default App;
